ail {\r\n          text-align: justify;\r\n          word-break:break-all;\r\n          margin-top: 5px;\r\n\r\n          & p {\r\n            padding: 10px;\r\n            border-radius: 10px;\r\n            background: #eeeeee;\r\n            border: 1px solid #333333;\r\n\r\n            & .normal,\r\n            & .mod {\r\n              color: #eeeeee;\r\n              margin-right: 2px;\r\n              padding: 5px 10px;\r\n              background: #333333;\r\n              border-radius: 10px;\r\n              display: inline-block;\r\n            }\r\n\r\n            & .critical {\r\n              color: #eeeeee;\r\n              margin-right: 2px;\r\n              padding: 5px 10px;\r\n              background: #147500;\r\n              border-radius: 10px;\r\n              display: inline-block;\r\n            }\r\n\r\n            & .fumble {\r\n              color: #eeeeee;\r\n              margin-right: 2px;\r\n              padding: 5px 10px;\r\n              background: #a80000;\r\n              border-radius: 10px;\r\n              display: inline-block;\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n\r\n    & .itemmessage {\r\n      text-align: justify;\r\n      margin-top: 10px;\r\n      color: #333333;\r\n      border-radius: 10px;\r\n    }\r\n  }\r\n}"],"sourceRoot":""}]);
